export const formCalculadora = document.getElementById("form-calculadora");
export const capitalAcumulado = document.getElementById("capital-acumulado");
export const tempoAnos = document.getElementById("tempo-anos");
export const aporteMensal = document.getElementById("aporte-mensal");
export const telefoneCalculadora = document.getElementById("telefone");
export const nomeCalculadora = document.getElementById("nome-calculadora");
export const SubmitCalculadora = document.getElementById("calculadora-submit");

export const poupanca = document.getElementById("poupanca");
export const selic = document.getElementById("selic");
export const cdb = document.getElementById("cdb");
export const lci = document.getElementById("lci");

export const poupancaRendimento = document.getElementById(
  "poupanca-rendimento"
);
export const selicRendimento = document.getElementById("selic-rendimento");
export const cdbRendimento = document.getElementById("cdb-rendimento");
export const lciRendimento = document.getElementById("lci-rendimento");

export const poupancaLiquido = document.getElementById(
  "poupanca-liquido"
);
export const selicLiquido = document.getElementById("selic-liquido");
export const cdbLiquido = document.getElementById("cdb-liquido");
export const lciLiquido = document.getElementById("lci-liquido");

export var poupancaRentabilidade =  0.0051;
export var selicRentabilidade = 0.010423;
export var cdbRentabilidade = 0.012372;
export var lciRentabilidade = 0.010125;

export var inputsFields = [
  capitalAcumulado,
  tempoAnos,
  aporteMensal,
  telefoneCalculadora,
  nomeCalculadora,
];

const pv = parseFloat(capitalAcumulado.value);
const meses = parseFloat(tempoAnos.value * 12);
const aporte = parseFloat(aporteMensal.value);

export function calcular() {
  if (checkSubmit()) {
    poupanca.innerText = formatarTexto(valorFinal(poupancaRentabilidade));
    selic.innerText = formatarTexto(valorFinal(selicRentabilidade));
    cdb.innerText = formatarTexto(valorFinal(cdbRentabilidade));
    lci.innerText = formatarTexto(valorFinal(0.01));

    poupancaRendimento.innerText = formatarTexto(rendimento(valorFinal(poupancaRentabilidade)));
    selicRendimento.innerText = formatarTexto(rendimento(valorFinal(poupancaRentabilidade)));
    cdbRendimento.innerText = formatarTexto(rendimento(valorFinal(poupancaRentabilidade)));
    lciRendimento.innerText = formatarTexto(rendimento(valorFinal(poupancaRentabilidade)));

    poupancaLiquido.innerText = formatarTexto(rendimento(valorFinal(poupancaRentabilidade)));
    selicLiquido.innerText = formatarTexto(rendimento(valorFinal(poupancaRentabilidade)));
    cdbLiquido.innerText = formatarTexto(descontoIR(rendimento(valorFinal(poupancaRentabilidade))));
    lciLiquido.innerText = formatarTexto(rendimento(valorFinal(poupancaRentabilidade)));


    

    return;
  }

  console.log("nao enviou");
  return;
}

export function checkIfEmpty(input) {
  if (input.value.length === 0) {
    return false;
  }
  return true;
}

export function checkSubmit() {
  let enviar = true;

  for (let i = 0; i < inputsFields.length; i++) {
    if (checkIfEmpty(inputsFields[i]) === false) {
      // console.log("Erro ao enviar");
      // console.log("Erro no input = " + i);
      inputsFields[i].previousSibling.style.color = "#990000";
      enviar = false;
    } else {
      inputsFields[i].previousSibling.style.color = "#000000";
    }
  }

  return enviar;
}

export function valorFinal(rentabilidadeValue) {
  if (tempoAnos.value < 0) {
    tempoAnos.value = 0;
  }

  var pv = parseFloat(capitalAcumulado.value);
  var meses = parseFloat(tempoAnos.value * 12);
  var aporte = parseFloat(aporteMensal.value);

  return (
    pv * Math.pow(1 + rentabilidadeValue, meses) +
    (aporte * (Math.pow(1 + rentabilidadeValue, meses) - 1)) /
      rentabilidadeValue
  );
}

export function rendimento(valorFinal) {
  var pv = parseFloat(capitalAcumulado.value);
  var meses = parseFloat(tempoAnos.value * 12);
  var aporte = parseFloat(aporteMensal.value);

  return valorFinal - (pv + meses * aporte);
}

export function formatarTexto(value) {
  return (
    "R$ " +
    value
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ".")
  );
}

export function descontoIR(value) {
  return value * 0.85;
}
